import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ViewController, ModalController, AlertController, LoadingController, Loading } from 'ionic-angular';
import { ErrorPage } from './../error/error';

/**
 * Generated class for the GenricPopupPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-genric-popup',
  templateUrl: 'genric-popup.html',
})
export class GenricPopupPage {

  label = '';
  lang = '';

  val;
  buttonLabel = "Add";
  editMode: boolean = false;
  index: number = -1;
  flag = 2;
  
  /////////////////////////////// For String Arrays
  
  list = [];
  
  /////// Added for activity templates ////
  activity$;
  public loading: Loading;

  constructor(
                public navCtrl: NavController
              , public navParams: NavParams
              , private viewCtrl: ViewController
              , public loadingCtrl: LoadingController
              , private mdlCtrl: ModalController
              , public alertCtrl: AlertController
              ) {
    this.activity$ = this.navParams.get('list');
    this.label = this.navParams.get('label');
    this.lang = this.navParams.get('lang');
    this.flag = this.navParams.get('flag');
    console.log(this.list);
    // this.list.subscribe((l) => {
    //   console.log(l);
      
    // })
    
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad GenricPopupPage');
  }

  add() {
    if(this.val == undefined || this.val == '') {
      alert('Cannot add blank');
      return;
    }
    if(this.editMode && this.index > -1 ) {
      this.list[this.index] = this.val;
      this.editMode = false;
      this.index = -1;
      this.buttonLabel = "Add";
      } else {
      this.list.push(this.val);
    }
    this.val = "";
    console.log(`The length is ${this.list.length}`);
  }
  remove(i) {
    this.list.splice(i, 1);
  }
  edit(i) {
    this.val = this.list[i];
    this.editMode = true;
    this.index = i;
    this.buttonLabel = "Modify";
  }
  save() {
    this.viewCtrl.dismiss(this.list);
  }

  /// For Itenary ////
  getActivityDetails(l) {
    let a = this.mdlCtrl.create('DynaPage', {function: 'activity', activity: l});
    a.onDidDismiss((data) => {
      if(data == null) {
        return;
      } else {
        this.viewCtrl.dismiss(data)
      }
    })
    a.present();
  }
}



